/*Experience*/

.experiencecontainer{

    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
}

.experiencecontainer > div{

    background: var(--color-bg-variant);
    padding: 2.4rem 5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.experiencecontainer > div:hover{

    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.experiencecontainer > div h3{

    text-align: center;
    margin-bottom: 2rem;
    color: var(--color-primary);
}

.experiencecontent{

    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
    row-gap: 2rem;
}


.experiencedetails{

    display: flex;
    gap: 2rem;
}

.experiencedetails-icon{

    margin-top: 6px;
    color: var(--color-primary);
}

/* Media queries for medium devices(Tablets) */

@media screen and (max-width: 1024px) {

    .experiencecontainer{

        grid-template-columns: 1fr;
    }

    .experiencecontainer > div{

        width: 80%;
        padding: 2rem;
        margin: 0 auto;
    }

    .experiencecontent{

        padding: 1rem;
    }

}

/* Media queries for small devices(Mobiles) */

@media screen and (max-width: 600px) {

    .experiencecontainer{

        gap: 1rem;
    }

    .experiencecontainer > div{

        width: 100%;
        padding: 2rem 1rem;
    }
}


